<template>
    <div class="safety" v-if="safetySetting">
        <div class="safety-info">
            <div class="safety-logo">
                <user-avatars class="img" :src="safetySetting.head"></user-avatars>
            </div>
            <div class="safety-text">
                <p><span class="tit">{{$L('会员账号')}}：</span>{{ safetySetting.username }}</p>
                <p><span class="tit">{{$L('会员昵称')}}：</span>{{ safetySetting.nickname }}</p>
                <p><span class="tit">{{$L('最近登录时间')}}：</span>{{ safetySetting.loginTime }}</p>
            </div>
            <router-link :to="{name:'accountInfo'}" class="info-item">
                <span class="bbx-font bbx-icon-iconfontjiantou2"></span>
            </router-link>
        </div>
        <div class="safetyLevel">
            <div class="safety-progress">
                <span class="tit">{{$L("安全等级")}}</span>
                <el-progress color="#2D8CF0" :show-text="false" :stroke-width="10" :percentage="safetySetting.percent"></el-progress>
                <span class="size">{{gradeFunc(safetySetting.grade)}}</span>
            </div>
            <table class="table">
                <tr class="tr" v-if="safetySetting.password && safetySetting.password.hasOwnProperty('isset')">
                    <td class="td" width="120px">{{$L("登录密码")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.password.isset === 1"  @click="modifyPwd">
                            <span class="green span">{{$L("已设置")}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                        <p v-else-if="safetySetting.password.isset === 0"  @click="setPwd">
                            <span class="red span">{{$L("未设置")}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                    </td>
                </tr>
                <tr class="tr" v-if="safetySetting.hasOwnProperty('email')">
                    <td class="td">{{$L("邮箱绑定")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.email.isset === 1"  @click="modifyEmail">
                            <span class="green span">{{$L("已绑定")}}{{hidePhone(safetySetting.email.emailNum)}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                        <p v-else-if="safetySetting.email.isset === 0"  @click="setEmail">
                            <span class="red span">{{$L("未绑定")}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                    </td>
                </tr>
                <tr class="tr" v-if="safetySetting.hasOwnProperty('mobile')">
                    <td class="td">{{$L("手机绑定")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.mobile.isset === 1"  @click="modifyPhone">
                            <span class="green span">{{$L("已绑定")}}{{hidePhone(safetySetting.mobile.mobileNum)}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                        <p v-else-if="safetySetting.mobile.isset === 0"  @click="setPhone">
                            <span class="red span">{{$L("未绑定")}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                    </td>
                </tr>
                <tr class="tr" v-if="safetySetting.hasOwnProperty('wx') && safetySetting.wx.hasOwnProperty('isset')">
                    <td class="td">{{$L("关联微信账号")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.wx.isset === 1"  @click="unBindWeChat">
                            <span class="green span">{{$L("已关联")}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                        <p v-else-if="safetySetting.wx.isset === 0"  @click="bindWeChat">
                            <span class="red span">{{$L("未关联")}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                    </td>
                </tr>
                <tr class="tr" v-if="safetySetting.hasOwnProperty('qq') && safetySetting.qq.hasOwnProperty('isset')">
                    <td class="td">{{$L("关联QQ账号")}}</td>
                    <td class="td">
                        <p v-if="safetySetting.qq.isset === 1"  @click="untiedQQ">
                            <span class="green span">{{$L("已关联")}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                        <p v-else-if="safetySetting.qq.isset === 0"  @click="bindQQ">
                            <span class="red span">{{$L("未关联")}}</span><span class="bbx-font bbx-icon-iconfontjiantou2"></span>
                        </p>
                    </td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
	import bindPhone from '../../../components/account/bindPhone.vue';
	import bindPassword from '../../../components/account/bindPassword.vue';
	import bindEmail from '../../../components/account/bindEmail.vue';
    import bindQQ from '../../../components/account/bindQQ.vue';
    import bindWeChat from '../../../components/account/bindWeChat.vue';
    import userAvatars from '../../../components/common/userAvatars.vue';
    export default {
        name: "safety",
        components:{
            bindEmail,bindPassword,bindPhone,userAvatars,bindQQ,bindWeChat
        },
        data(){
            return {
                safetySetting:{}
            }
        },
        created () {
            this.getData();
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            getData() {
                const _self = this;
                _self.loading = true;
                _self.$api('safetySetting',{}, function (reData) {
                    if (reData.flag === 1) {
                        _self.safetySetting = reData.data;
                        _self.percentFunc();
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.loading = false;
                            _self.showError(_self.$L('登录超时'),_self.$L('请重新登录'),'error');
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            if (action === 'cancel') {
                                _self.$router.push({name: 'home'});
                            } else {
                                _self.loading = false;
                                _self.showError(_self.$L('网络请求失败'),_self.$L('请刷新页面重试'),'error');
                            }
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        if (action === 'cancel') {
                            _self.$router.push({name: 'home'});
                        } else {
                            _self.loading = false;
                            _self.showError(_self.$L('网络请求失败'),_self.$L('请刷新页面重试'),'error');
                        }
                    });
                });
            },
            percentFunc(){
                const _self = this;
                let i = 0;
                let count = 0;
                if (_self.safetySetting.hasOwnProperty('password')) {
                    count++;
                    if(_self.safetySetting.password.isset === 1) i++;
                }

                if (_self.safetySetting.hasOwnProperty('email')) {
                    count++;
                    if(_self.safetySetting.email.isset === 1) i++;
                }

                if (_self.safetySetting.hasOwnProperty('mobile')) {
                    count++;
                    if(_self.safetySetting.mobile.isset === 1) i++;
                }

                if (_self.safetySetting.hasOwnProperty('wx')) {
                    count++;
                    if(_self.safetySetting.wx.isset === 1) i++;
                }

                if (_self.safetySetting.hasOwnProperty('qq')) {
                    count++;
                    if(_self.safetySetting.qq.isset === 1) i++;
                }
                _self.safetySetting.percent = i/count*100;
            },
            gradeFunc(g){
                if(g === 1){
                    return this.$L('低');
                }else if(g === 2){
                    return this.$L('中');
                }else if(g === 3){
                    return this.$L('高');
                }
            },
            hidePhone(phone){
                return phone.substr(0, 3) + '****' + phone.substr(7);
            },
			modifyPhone(){
			    this.$store.commit('setDialogObj', {
			        name: this.$L('修改手机'),
			        view: bindPhone,
			        width: '500px',
			        data: {
			            isset:this.safetySetting.mobile.isset,
			            mobileNum:this.safetySetting.mobile.mobileNum,
			            trueMobileNum: this.safetySetting.mobile.trueMobileNum,
			        },
			    });
			},
			setPhone(){
			    this.$store.commit('setDialogObj', {
			        name: this.$L('绑定手机'),
			        view: bindPhone,
			        width: '500px',
			        data: {
			            isset:this.safetySetting.mobile.isset,
			            mobileNum:this.safetySetting.mobile.mobileNum,
			            trueMobileNum: this.safetySetting.mobile.trueMobileNum,
			        },

			    });
			},
			modifyPwd(){
			    this.$store.commit('setDialogObj', {
			        name: this.$L('修改密码'),
			        view: bindPassword,
			        width: '500px',
			        data: {
			            isset:this.safetySetting.password.isset,
                        phoneSet:this.safetySetting.hasOwnProperty('mobile') && this.safetySetting.mobile.isset === 1 ? 1 : 0,
			        }
			    });
			},
			setPwd(){
			    this.$store.commit('setDialogObj', {
			        name: this.$L('设置密码'),
			        view: bindPassword,
			        width: '500px',
			        data: {isset:this.safetySetting.password.isset}

			    });
			},
			modifyEmail(){
			    this.$store.commit('setDialogObj', {
			        name: this.$L('修改邮箱'),
			        view: bindEmail,
			        width: '500px',
			        data: {
			            isset:this.safetySetting.email.isset,
			            emailNum:this.safetySetting.email.emailNum,
			        }
			    });
			},
			setEmail(){
			    this.$store.commit('setDialogObj', {
			        name: this.$L('绑定邮箱'),
			        view: bindEmail,
			        width: '500px',
			        data: {
			            isset:this.safetySetting.email.isset,
			            emailNum:this.safetySetting.email.emailNum,
			        }
			    });
			},
			bindQQ(){
			    this.$store.commit('setDialogObj', {
			        name: this.$L('绑定QQ'),
			        view: bindQQ,
			        width: '815px',
			        height: '560px',
			    });
			},
			untiedQQ() {
                const _self = this;
                _self.$confirm(_self.$L('是否解除绑定？'), _self.$L('解绑QQ'), {
                    confirmButtonText: _self.$L('立即解绑'),
                    cancelButtonText: _self.$L('我再想想'),
                    type: 'error',
                    distinguishCancelAndClose: true
                }).then(() => {
                    _self.loading = true;
                    _self.$getApi('untiedQQ',{}, function (reData) {
                        _self.$message({
                            message: _self.$L('解绑QQ成功!'),
                            type: 'success'
                        });
                        _self.loading = false;
                        _self.getData();
                    }, function (error) {
                        if(error && error.flag == -2){
                            _self.$alert(_self.$L('尚未设置登录密码！无法解绑！'), _self.$L('解绑QQ失败!'), {
                                confirmButtonText: _self.$L('确定'),
                                type: 'error',
                                showClose: false
                            }).then(() => {
                                _self.loading = false;
                                _self.getData();
                            });
                        }else{
                            _self.$confirm(_self.$L('解绑QQ失败!'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.untiedQQ();
                            }).catch(action => {
                                _self.loading = false;
                                _self.getData();
                            });
                        }
                    });
                }).catch(action => {

                });
			},
			bindWeChat() {
			    this.$store.commit('setDialogObj', {
			        name: this.$L('绑定微信'),
			        view: bindWeChat,
			        width: '815px',
			        height: '560px',
                    closeFunc:()=>{
                        this.getData();
                    }
			    });
			},
            unBindWeChat() {
                const _self = this;
                _self.$confirm(_self.$L('是否解除绑定？'), _self.$L('解绑微信'), {
                    confirmButtonText: _self.$L('立即解绑'),
                    cancelButtonText: _self.$L('我再想想'),
                    type: 'error',
                    distinguishCancelAndClose: true
                }).then(() => {
                    _self.loading = true;
                    setTimeout(()=>{
                        //防止弹窗覆盖
                        _self.$getApi('unBindWeChat',{}, function (reData) {
                            _self.$message({
                                message: _self.$L('解绑微信成功!'),
                                type: 'success'
                            });
                            _self.loading = false;
                            _self.getData();
                        }, function (error) {
                            if(error && error.flag == -2){
                                _self.$alert(_self.$L('尚未设置登录密码！无法解绑！'), _self.$L('解绑微信失败!'), {
                                    confirmButtonText: _self.$L('确定'),
                                    type: 'error',
                                    showClose: false
                                }).then(() => {
                                    _self.loading = false;
                                    _self.getData();
                                });
                            }else{
                                _self.$confirm(_self.$L('解绑微信失败!'), _self.$L('出错了'), {
                                    confirmButtonText: _self.$L('点击重试'),
                                    cancelButtonText: _self.$L('取消'),
                                    type: 'error',
                                    distinguishCancelAndClose: true
                                }).then(() => {
                                    _self.unBindWeChat();
                                }).catch(action => {
                                    _self.loading = false;
                                    _self.getData();
                                });
                            }
                        });
                    },500);
                }).catch(action => {

                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/icon/iconfont.css";
    @import "../../../assets/css/common/lib.scss";
    .safety{
        .safety-info{
            position: relative;
            background-color: #ffffff;
            width: 100%;
            height: 120px;
            .safety-logo {
                top: 50%;
                position: absolute;
                transform: translateY(-50%);
                overflow: hidden;
                width: 80px;
                height: 80px;
                box-sizing: border-box;
                border-radius: 100%;
                left: 20px;
                padding: 10px;
                @extend .no-select;
                .img {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                    background-size: 80% !important;
                    background-position: center;
                    background-repeat: no-repeat;
                }
                /deep/ .img-error {
                    width: 100%;
                    height: 100%;
                    border-radius: 50%;
                }
                /deep/ .el-image__inner{
                    border-radius: 50%;
                }
            }
            .safety-text{
                top: 50%;
                position: absolute;
                transform: translateY(-50%);
                font-size: 12px;
                line-height: 1.8;
                left: 100px;
                .tit{
                    display: inline-block;
                    width: 85px;
                }
            }
            .info-item{
                position: absolute;
                width: 100%;
                height: 120px;
                right: 0;
            }
            .bbx-font{
                float: right;
                margin-right: 30px;
                margin-top: 47px;
            }
        }
        .safetyLevel{
            margin-top: 10px;
            padding-bottom: 20px;
            .tit{
                display: inline-block;
                width: 80px;
                text-align: center;
            }
            .safety-progress{
                font-size: 14px;
                height: 60px;
                line-height: 60px;
                padding-left: 17px;
                background-color: #ffffff;
                .el-progress{
                    width: 50%;
                    display: inline-block;
                    vertical-align: middle;
                }
                .size{
                    padding-left: 20px;
                    font-size: 14px;
                    color: #2D8CF0;
                    vertical-align: middle;
                }
            }
            .table{
                width: 100%;
                background-color: #ffffff;
                .tr{
                    font-size: 12px;
                    border-top: 1px solid #E1E1E1;
                    border-bottom: 1px solid #E1E1E1;
                    .td{
                        line-height: 60px;
                        vertical-align: middle;
                    }
                    .td:nth-child(1){
                        padding-left: 30px;
                        text-align: left;
                    }
                    .td:nth-child(2){
                        cursor: pointer;
                        text-align: right;
                        padding-right: 30px;
                        .span{
                            font-size: 12px;
                            color: #2D8CF0;
                        }
                        .bbx-font{
                            font-size: 16px;
                            color: #8A8A8A;
                            padding-left: 10px;
                        }
                    }
                }
                .green{
                    color: #008000!important;
                }
                .red{
                    color: #FF0000!important;
                }
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>
<style>
    @media screen and (max-width:767px) {
        .el-message-box {
            width: 94% !important;
        }
    }
</style>
